import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-reservation-past',
  templateUrl: './reservation-past.component.html',
  styleUrls: ['./reservation-past.component.scss']
})
export class ReservationPastComponent implements OnInit {

  constructor() { }

 
  ngOnInit() {
    $(document).ready(function(){
      let slide="none";
      $("#down-past").click(function(){
      
      if(slide=="none"){
        $(".slide-down").slideDown();
         slide="block";
      }else{
        $(".slide-down").slideUp();
        slide="none";
      }
      })
$(".rate-box-item").click(function() {
  $("#back-rate").css("display", "flex")
})
$(".submit , #submit-back-three ,#submit-back-one , #submit-back-two").click(function() {
  $("#back-rate").fadeOut();
})
$(".rate-chose").click(function() {
  $(this).addClass("rate-active");
  let idChose = $(this).attr("id");
  $(".rate-chose").not(this).removeClass("rate-active");
  $(".rate-chose").not(this).addClass("rateNotChose");
  $(".descriptionChose").fadeOut();
  $("#" + idChose + "-description").fadeIn();
})
$(document).mouseup(function (e)
{
  var container = $(".box-rate");
  if (!container.is(e.target) 
      && container.has(e.target).length === 0)
  {
      $("#back-rate").hide();
  }
});
});
}

}
