import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-sign-up-two',
  templateUrl: './sign-up-two.component.html',
  styleUrls: ['./sign-up-two.component.scss']
})
export class SignUpTwoComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
    {$(document).ready(function(){
  
      $(".toggle-password").click(function() {
        $(this).toggleClass("fa-eye fa-eye-slash");
        var toggle = $(this).attr("toggle");
        let stateInput = $("." + toggle).attr("type");
        if (stateInput == "password") {
            $("." + toggle).attr("type", "text");
        } else {
            $("." + toggle).attr("type", "password");
        }
    });
  })
  }

}
}