import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.scss']
})
export class EditProfileComponent implements OnInit {

  constructor() { }
  ngOnInit() {
    $(document).ready(function(){
      function readURL(input) {
        if (input.files && input.files[0]) {
            var reader = new FileReader();
            reader.onload = function(e) {
                $('#imagePreview').css('background-image', 'url(' + e.target.result + ')');
                $('#imagePreview').hide();
                $('#imagePreview').fadeIn(650);
            }
            reader.readAsDataURL(input.files[0]);
        }
    }
    $("#imageUpload").change(function() {
        readURL(this);
    });
    
    $("#Next-Verit").click(function() {
        $(this).fadeOut();
        $("#just-phone").fadeOut();
        $("#save-number").fadeIn();
      $("#verity").css({ display: "flex" });

  })
  $("#back-to-phone").click(function(){
    $("#back-one-phone").fadeIn();
  $("#save-number").fadeOut();
  $("#just-phone ,#Next-Verit").fadeIn();
  $("#verity").css({ display: "none" });
  })
  $("#save-item-phone , #boxphoneButton , #back-one-phone").click(function() {
      $("#box-phone-change").fadeOut();
      $("#back-one-phone").fadeIn();
      $("#save-number").fadeOut();
      $("#just-phone ,#Next-Verit").fadeIn();
      $("#verity").css({ display: "none" });
  })
    $("#change-passWord").click(
      function() {
          $("#box-password-change").css({ display: "flex" })
      }
  )
  $("#change-phone").click(
      function() {
          $("#box-phone-change").css({ display: "flex" })
      }
  )
  $("#save-change-pass , #boxpasswordButton ,#back-password").click(function() {
      $("#box-password-change").css({ display: "none" })
  })

  $(".toggle-password ").click(function() {
      $(this).toggleClass("fa-eye fa-eye-slash");
      var toggle = $(this).attr("toggle");
      let stateInput = $("." + toggle).attr("type");
      if (stateInput == "password") {
          $("." + toggle).attr("type", "text");
      } else {
          $("." + toggle).attr("type", "password");
      }
  });
  $("#flatpickr").focus(function(){
      $(this).attr("type","date")
  })
  $(document).mouseup(function (e)
    {
      var container = $(".small-box");
      if (!container.is(e.target) 
          && container.has(e.target).length === 0)
      {
          $("#box-password-change").hide();
      }
    });
    $(document).mouseup(function (e)
    {
      var container = $(".small-box");
      if (!container.is(e.target) 
          && container.has(e.target).length === 0)
      {
          $("#box-phone-change").hide();
          $("#back-one-phone").fadeIn();
          $("#save-number").fadeOut();
          $("#just-phone ,#Next-Verit").fadeIn();
          $("#verity").css({ display: "none" });
      }
    });
  })}
}
