import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { OwlOptions } from 'ngx-owl-carousel-o';

@Component({
  selector: 'app-agenda',
  templateUrl: './agenda.component.html',
  styleUrls: ['./agenda.component.scss']
})
export class AgendaComponent implements OnInit {

  customOptions: OwlOptions = {
    loop: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    nav: true,
    rtl : true,
    navText: ['<i class="fas fa-arrow-left bg-white shadow rounded-circle p-2"></i>', '<i class="fas fa-arrow-right  bg-white shadow rounded-circle p-2"></i>'],
    responsive: {
      0: {
        items: 1 
      },
      70: {
        items: 2
      },
      120: {
        items: 3
      },
      200: {
        items: 4
      },
      300: {
        items: 5
      },
      450: {
        items: 5
      }
    }
  }

  constructor() { 
  }

  
  public ngOnInit()
  {

    $(document).ready(function(){

      $(".Reservation-home-schedule .day").click(function () {
        $(this).addClass("activeDay");
        $(".Reservation-home-schedule .day").not(this).removeClass('activeDay');
    })
  
   
  var dotsStatus = $(".drop-down-agenda").css("display");

      let myItemsDots = Array.from(document.getElementsByClassName("dots"));
      let myItemsDropDown = Array.from(document.getElementsByClassName("drop-down-agenda"));
      
      for (let i = 0; i < myItemsDots.length; i++) {
          myItemsDots[i].addEventListener("click", function(e) {
              if (dotsStatus == "none") {
      
                $(myItemsDropDown[i]).css({display:"block"});
                dotsStatus = "block";
              } else {
                $(myItemsDropDown[i]).css({display:"none"});
                  dotsStatus = "none";
      
              }
          })
      }
     
      $("body").click(function() {
          var $target = $(event.target);
          if (!$target.is(".dots")) {
              $("body").find(".drop-down-agenda").fadeOut();
              //
          }
      });    
      $(".modify").click(function() {
        $("#box-time").css({ display: "flex" })
    })
    $("#back , #boxCloseButton").click(function() {
            $("#box-close-Cancel-Available").fadeOut(1000);
        })
        $(document).mouseup(function (e)
        {
          var container = $(".box-close-Cancel-Available");
          if (!container.is(e.target) 
              && container.has(e.target).length === 0)
          {
              $("#box-close-Cancel-Available").hide();
          }
        });

        $(".cancel-Available").click(function() {
          $("#box-close-Cancel-Available").css({display:"flex"});
      })
      let dirHtml =$("html").attr("dir");
      if(dirHtml == "ltr"){
        $(".owl-stage").css({transform : "translate3d(10px, 0px, 0px"})

      }
    $("#modify-button , #boxTimeButton , #modify-back").click(function() {
        $("#box-time").fadeOut(1000);
    })
    $(document).mouseup(function (e)
    {
      var container = $(".box-chose-time");
      if (!container.is(e.target) 
          && container.has(e.target).length === 0)
      {
          $("#box-time").hide();
      }
    });

      /* */
});
}

  /*
    */

}
