import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.scss']
})
export class SignInComponent implements OnInit {

  constructor() { }

 
  public ngOnInit()
  {$(document).ready(function(){
    function readURL(input) {
      if (input.files && input.files[0]) {
          var reader = new FileReader();
          reader.onload = function(e) {
              $('#imagePreview').css('background-image', 'url(' + e.target.result + ')');
              $('#imagePreview').hide();
              $('#imagePreview').fadeIn(650);
          }
          reader.readAsDataURL(input.files[0]);
      }
  }
  $("#imageUpload").change(function() {
      readURL(this);
  });
  $(".toggle-password").click(function() {
    $(this).toggleClass("fa-eye fa-eye-slash");
    var toggle = $(this).attr("toggle");
    let stateInput = $("." + toggle).attr("type");
    if (stateInput == "password") {
        $("." + toggle).attr("type", "text");
    } else {
        $("." + toggle).attr("type", "password");
    }
});
$("#flatpickr-birthdate").focus(function(){
  $(this).attr("type","date")
})
//next one
//done last
//continue
    $(".button-form").click(function(){
      let idButton = $(this).attr("id");
      if(idButton == "next")
      {
        $(".sign-form").hide(); 
        $(".sign-in-form-two").show();
      }else if(idButton == "continue")
      {
        $(".sign-form").hide(); 
        $(".sign-in-form-three").show();
      }
    })
});
}


}
